<template>
	<view class="tab_box">
		<view class="tit">{{title}}</view>
		<view class="tab_item_box">
			<view v-for="item in tabs" :key='item.id'
			:class="['tab_item flex_center_row',{'cur':item.checked}]"
			@click="changeType(item)">
				{{item.value}}
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				
			};
		},
		props:{
			title:{
				type:String,
				default:'tab标题'
			},
			tabs:{
				type: Array,
				default: [
					{	
						id:0,
						value:'类型1',
						checked:true
					},
					{	
						id:1,
						value:'类型2',
						checked:false
					},
					{
						id:2,
						value:'类型3',
						checked:true
					}
				]
			}
		},
		onShow(){
			// console.log(this.title)
			// console.log(this.tabs)
		},
		methods:{
			changeType(item){
				item.checked = !item.checked
				// console.log(this.tabs)
				this.$emit('update:tabs',this.tabs)
			}
		}
	}
</script>

